body{
  /*position: relative;*/
  height: 100%;
  width: 100%;
  background-color: #20fffa;
  overflow: hidden;
  /*background:#2EB5E5 ;*/
}

.main {
  position: absolute;
  max-width: 600px;
  height:100%;
  display:table;
  transform: translate(-50%,0);
  -webkit-transform: translate(-50%,0);
  -ms-transform: translate(-50%,0);
  left: 50%;
  width: 100%;
  overflow: hidden;
}

.inviteCard{
  display:table-cell;
  vertical-align:middle;
  width:100%;
  height:100%;
}

.car {
  -webkit-animation: carride 15s infinite linear;
  animation: carride 15s linear infinite;
  z-index:999;
  position:absolute;
  bottom:0;
}

.car-base {
  position:absolute;
  bottom:10px;
  right:45px;
  width:80px;
  height:15px;
  background:#D35400;
  background: -webkit-gradient(linear, left top, left bottom, from(#D35400), to(#c14e03));
  background: -webkit-linear-gradient(#D35400, #c14e03);
  background: -o-linear-gradient(#D35400, #c14e03);
  background: linear-gradient(#D35400, #c14e03);
  z-index:1;
  -webkit-transform: perspective(100px) rotateX(-25deg);
  -webkit-transform-origin: left center;
  -moz-transform: perspective(100px) rotateX(-25deg);
  -moz-transform-origin: left center;
}

.car-wheel-one {
  width:10px;
  height:10px;
  border-radius:50px;
  border-left:2px solid white;
  border-right:2px solid #D35400;
  border-top:2px solid white;
  border-bottom:2px solid #D35400;
  background-color:black;
  position:absolute;
  bottom:4px;
  right:57px;
  z-index:12;
  -webkit-animation: wheel-spin 1.1s infinite linear;
}

.car-wheel-two {
  width:10px;
  height:10px;
  border-radius:50px;
  border-left:2px solid white;
  border-right:2px solid #D35400;
  border-top:2px solid white;
  border-bottom:2px solid #D35400;
  background-color:black;
  position:absolute;
  bottom:4px;
  right:95px;
  z-index:2;
  -webkit-animation: wheel-spin 1.1s infinite linear;
}

.car-roof {
  width:50px;
  height:25px;
  background:#D35400;
  position:absolute;
  bottom:24px;
  right:55px;
  z-index:3;
  border-top-left-radius: 25px;
  border-top-right-radius: 25px;
}

.car-roof-inner {
  background:#F2F1EF;
  background: -webkit-gradient(linear, left top, left bottom, from(#F2F1EF), to(#dad9d8));
  background: -webkit-linear-gradient(#F2F1EF, #dad9d8);
  background: -o-linear-gradient(#F2F1EF, #dad9d8);
  background: linear-gradient(#F2F1EF, #dad9d8);
  width:30px;
  height:20px;
  border-top-left-radius: 15px;
  border-top-right-radius: 15px;
  margin-left:10px;
  margin-top:5px;
}

.car-roof-seperator {
  width:2px;
  height:100%;
  background-color:#D35400;
  margin-left:18px;
}

.car-light {
  width: 0;
  height: 0;
  border-top: 25px solid transparent;
  border-bottom: 10px solid transparent;
  border-left: 55px solid #fdf0d2;
  position:absolute;
  bottom:6px;
  right:125px;
  z-index:9;
  opacity:0.3;
}

.overlay-shadow {
  background-color:black;
  width:93px;
  height:25px;
  position:absolute;
  bottom:150px;
  left:50px;
  z-index:10;
  opacity:0.1;
}

.overlay-shadow-two {
  background-color:black;
  width:170px;
  height:7px;
  position:absolute;
  top:0px;
  left:0px;
  z-index:10;
  opacity:0.1;
}

