.house-roof {
  width: 0;
  height: 0;
  border-left: 65px solid transparent;
  border-right: 65px solid transparent;
  border-bottom: 65px solid #22313F;
  position:absolute;
  top:-62px;
  left:-24px;
  z-index:1;
}

.house-development-platform {
  background-color:white;
  height:2px;
  width:100%;
  border-top:#22313F 1px solid;
  border-bottom:#22313F 1px solid;
  position:absolute;
  bottom:0;
  left:0;
  z-index:35;
}

.tree {
  background-color:#86421c;
  width:15px;
  height:65px;
  position:absolute;
  bottom:0;
  left:25px;
}

.tree-top {
  width: 0;
  height: 0;
  border-left: 30px solid transparent;
  border-right: 30px solid transparent;
  border-bottom: 100px solid #049372;
  position:absolute;
  bottom:25px;
  left:-22px;
}

.tree-shadow {
  background-color:black;
  width:15px;
  height:10px;
  position:absolute;
  bottom:15px;
  z-index:10;
  opacity:0.1;
}

.house-one {
  width:85px;
  height:80%;
  background: #EB9532;
  background: -webkit-gradient(linear, left top, left bottom, from(#EB9532), to(#c27b28));
  background: -webkit-linear-gradient(#EB9532, #c27b28);
  background: -o-linear-gradient(#EB9532, #c27b28);
  background: linear-gradient(#EB9532, #c27b28);
  position:absolute;
  bottom:0;
  left:50px;
  border-top:solid 3px #E87E04;
  border-left:solid 3px #E87E04;
  border-right:solid 3px #E87E04;
}

.door {
  height:50px;
  width:30px;
  border:4px solid #22313F;
  position:absolute;
  bottom:0px;
  left:7px;
}

.door-inner {
  background-color:white;
  width:24px;
  height:44px;
  border:3px solid #E87E04;
}

.door-inner-2x {
  background-color:white;
  width:18px;
  height:38px;
  border:3px solid;
}

.first-level-window-left {
  width: 20px;
  height: 40px;
  background-color: white;
  position: absolute;
  left: 10px;
  top: 20px;
  border-top: 2px solid #22313F;
  border-left: 2px solid #22313F;
  border-right: 2px solid #22313F;
}

.windowsill {
  height:2px;
  width:30px;
  background-color:#22313F;
  position:absolute;
  bottom:-2px;
  left:-5px;
}

.first-level-window-right {
  width:20px;
  height:40px;
  background-color:white;
  position:absolute;
  left:50px;
  top:20px;
  border-top:2px solid #22313F;
  border-left:2px solid #22313F;
  border-right:2px solid #22313F;
}

.window-half-closed {
  width:20px;
  height:20px;
  background-color:#22313F;
  position:absolute;
  bottom:0;
}

.windowsill {
  height:2px;
  width:30px;
  background-color:#22313F;
  position:absolute;
  bottom:-2px;
  left:-5px;
}

/*!*CSS Animations *!*/

@-webkit-keyframes carride {
  from {
    width: 100%;
    /*-webkit-transform: translateX(1vw);*/
  }
  to {
    width: 0%;
    /*-webkit-transform: translateX(-100px);*/
  }
}

@-webkit-keyframes wheel-spin {
  from {-webkit-transform: rotate(359deg);}
  to   {-webkit-transform: rotate(0deg);}
}

a{
  color: black;
}

a:hover, a:active{
  color: crimson;
  text-decoration:none;
}

ul {
  list-style: none;
}

h1 {
  font-size: 6vh;
}

p {
  font-size: 3vh;
}

.sun {
  position: absolute;
  right: 30%;
  top: 10%;
  margin: auto;
  width:70px;
  height:70px;
  border-radius:50%;
  background:white;
  opacity:0.9;
  -webkit-box-shadow: 0px 0px 40px 15px white;
          box-shadow: 0px 0px 40px 15px white;
}

.ray_box {
  position: absolute;
  margin: auto;
  top:0px;
  left:0;
  right:0;
  bottom:0;
  width:70px;
  -webkit-animation: ray_anim 120s linear infinite;
  animation: ray_anim 120s linear infinite;
}
.ray {
  background: -webkit-linear-gradient(top, rgba(255,255,255,0) 0%, rgba(255,255,255,0.8) 50%, rgba(255,255,255,0) 100%);
  background: -webkit-gradient(linear, left top, left bottom, from(rgba(255,255,255,0)), color-stop(50%, rgba(255,255,255,0.8)), to(rgba(255,255,255,0)));
  background: -o-linear-gradient(top, rgba(255,255,255,0) 0%, rgba(255,255,255,0.8) 50%, rgba(255,255,255,0) 100%);
  background: linear-gradient(top, rgba(255,255,255,0) 0%, rgba(255,255,255,0.8) 50%, rgba(255,255,255,0) 100%);
  margin-left:10px;
  border-radius:80% 80% 0 0;
  position:absolute;
  opacity:0.7;
}

.ray1 {
  height:170px;
  width:30px;
  -webkit-transform: rotate(180deg);
  top:-175px;
  left: 15px;
}
.ray2 {
  height:100px;
  width:8px;
  -webkit-transform: rotate(220deg);
  top:-90px;
  left: 75px;
}
.ray3 {
  height:170px;
  width:50px;
  -webkit-transform: rotate(250deg);
  top:-80px;
  left: 100px;
}
.ray4 {
  height:120px;
  width:14px;
  -webkit-transform: rotate(305deg);
  top:30px;
  left: 100px;
}
.ray5 {
  height:140px;
  width:30px;
  -webkit-transform: rotate(-15deg);
  top:60px;
  left: 40px;
}
.ray6 {
  height:90px;
  width:50px;
  -webkit-transform: rotate(30deg);
  top:60px;
  left: -40px;
}
.ray7 {
  height:180px;
  width:10px;
  -webkit-transform: rotate(70deg);
  top:-35px;
  left: -40px;
}
.ray8 {
  height:120px;
  width:30px;
  -webkit-transform: rotate(100deg);
  top:-45px;
  left:-90px;
}
.ray9 {
  height:80px;
  width:10px;
  -webkit-transform: rotate(120deg);
  top:-65px;
  left:-60px;
}
.ray10 {
  height:190px;
  width:23px;
  -webkit-transform: rotate(150deg);
  top:-185px;
  left: -60px;
}


@-webkit-keyframes ray_anim {
  0% { -webkit-transform: rotate(0deg); transform: rotate(0deg);}
  100% { -webkit-transform: rotate(360deg); transform: rotate(360deg);}
}



